<template>
  <div class="reg">
    <div class="content">
      <h1>欢迎注册</h1>
      <div class="content_a">
        <van-divider
            :style="{ color: '#000', borderColor: '#4c4f50', padding: '0 16px'}"
        >
          <router-link to="/login"><span>登录</span></router-link>
          <i></i>
          <router-link to=""><span>注册</span></router-link>
        </van-divider>
      </div>
      <div class="content_input">
        <van-form @submit="onSubmit">
          <van-field
              v-model="username"
              name="用户名"
              label="用户名"
              placeholder="请输入用户名"
              :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
              v-model="password"
              type="password"
              name="密码"
              label="密码"
              placeholder="请输入密码"
              :rules="[{ required: true, message: '请填写密码' }]"
          />
          <van-field v-model="nikename" label="昵称" placeholder="请输入昵称(可选)"/>
          <van-field name="uploader" label="头像上传">
            <template #input>
              <van-uploader :after-read="afterRead"  v-model="uploader" max-count="1"/>
            </template>
          </van-field>
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" color="#f55657">注册</van-button>
          </div>
          <p style="color:#000;position:fixed;bottom:0.05rem;left:50%;transform: translateX(-50%)">遇到问题</p>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "reg",
  data() {
    return {
      username: "",
      password: "",
      uploader: [],
      nikename: "",
      file:{}
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
    onSubmit(values) {
      console.log('submit', values);
      let data = new FormData()
      data.append("username",this.username)
      data.append("password",this.password);
      data.append("nikename",this.nikename);
      data.append("file",this.file)
      this.$axios({
        url: "/reg",
        method: "post",
        data
      }).then(res => {
        console.log(res)
        if(res.data.err){
          console.log(res.data.title)
        }else{
          console.log(res.data)
          const d = new Date();
          d.setDate(d.getDate() + 1);
          // document.cookie = `token=${res.data.data.token};expires=${d}`;
          this.$router.push('/login')
        }

      })
    },
    afterRead(file){
      this.file = file.file;
    }

  }
}
</script>

<style scoped lang="scss">

.reg {

  .content {

    h1 {
      font-size: 30px;
      letter-spacing: 5px;
      text-align: center;
      color:#4c4f50;
      margin: 0.8rem auto 0.5rem;
    }

    .content_a {
      margin: 30px 0;

      span {
        font-size: 20px;
      }

      i {
        width: 1px;
        height: 20px;
        background: #000;
        margin: 0 5px;
      }

    }

    .content_input {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

  }
}
</style>